.App-left {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-content: center;
  width: 20%;
  height: 1023px;
  flex-shrink: 0;

  /* background: url('../public/Cloud-background.png') repeat-x; */
  float: left;
  background-color: #1E213A;
}

.App-right {
  
  width: 80%;
  height: 1023px;
  flex-shrink: 0;
  background: #100E1D;
  float: right;
}

.App-weather {
  box-sizing: border-box;
  text-align: center;
  margin-top: 20%;

}

img {
  width: 100%;
  height: 100%;
}

.App-number {
  color: white;
  font-family: Raleway;
  font-size: 48px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.App-status {
  color: #A09FB1;
  text-align: center;
  font-family: Raleway;
  font-size: 36px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.App-weather-date {
margin-top: 10px;
  color: #88869D;
  font-family: Raleway;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.App-location{
  color: #88869D;
font-family: Raleway;
font-size: 18px;
font-style: normal;
font-weight: 600;
line-height: normal;
}

.App-weather-days{
  display: flex;
  flex-wrap: wrap;
  justify-content:center;
}

.App-weather-day,.App-weather-hightlights{
  color: #88869D;
  width: 120px;
  height: 177px;
  text-align: center; 
  margin-top: 100px;
  border: #A09FB1 solid 1px;
  background: #1E213A; 
  margin-left: 2%;
  }

  .App-weather-hightlights{
    margin-top: 60px;
    width: 350px;
    
    }

  .App-weather-title{
    margin-top: 60px;
    margin-left: 27%;
    color:white;
  }

  .App-weather-small-image{
    width: 75%;

  }

@media (max-width: 767px) {
  .App-left, .App-right {
    width: 100%;
  }

  .App-right {

    height: 1710px;
  }
}